import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams , ModalController} from 'ionic-angular';

@IonicPage()
@Component({
  selector: 'page-breakfast',
  templateUrl: 'breakfast.html',
})
export class BreakfastPage {

  constructor(private modal: ModalController , public navCtrl: NavController, public navParams: NavParams) {
  }
openModal(){
  const myModal = this.modal.create('ModalPage');

  myModal.present();
}
  breakfast(){
    this.navCtrl.push('MealPage');
  }

 dinner() {
    this.navCtrl.push('MealPage');
  }

  lunch(){
    this.navCtrl.push('StatisticPage');

  }

  other(){
    this.navCtrl.push('ViewprofilePage');

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad BreakfastPage');
  }

  
}



